import type { MessageEntity } from './packages/tgx-core/src/Client/Managers/MessagesManager'
import type { InputMessageContent } from './packages/tgx-core/src/Builders/Input/Content/InputMessageContent'
import type { InlineKeyboardMarkup } from './packages/tgx-core/src/Builders/InlineKeyboardMarkup'

import { BaseBuilder } from './packages/tgx-core/src/Builders/BaseBuilder'

export interface InlineQueryResultDocumentPayload {
    type: string
    id: string
    title: string
    caption?: string
    parse_mode?: string
    caption_entities?: MessageEntity[]
    document_url: string
    mime_type: 'application/pdf' | 'application/zip'
    description?: string
    reply_markup?: InlineKeyboardMarkup
    input_message_content?: InputMessageContent
    thumbnail_url?: string
    thumbnail_width?: number
    thumbnail_height?: number
}

export class InlineQueryResultDocument extends BaseBuilder<InlineQueryResultDocumentPayload> implements InlineQueryResultDocumentPayload {
    
    public type: string = 'document'

    public declare id: string
    public declare title: string
    public declare caption?: string
    public declare parse_mode?: string
    public declare caption_entities?: MessageEntity[]
    public declare document_url: string
    public declare mime_type: 'application/pdf' | 'application/zip'
    public declare description?: string
    public declare reply_markup?: InlineKeyboardMarkup
    public declare input_message_content?: InputMessageContent
    public declare thumbnail_url?: string
    public declare thumbnail_width?: number
    public declare thumbnail_height?: number

    public setId(id: string): this {
        this.id = id
        return this
    }

    public setTitle(title: string): this {
        this.title = title
        return this
    }

    public setCaption(caption?: string): this {
        this.caption = caption
        return this
    }

    public setParseMode(parseMode?: string): this {
        this.parse_mode = parseMode
        return this
    }

    public setCaptionEntities(captionEntities?: MessageEntity[]): this {
        this.caption_entities = captionEntities
        return this
    }

    public setDocumentUrl(documentUrl: string): this {
        this.document_url = documentUrl
        return this
    }

    public setMimeType(mimeType: 'application/pdf' | 'application/zip'): this {
        this.mime_type = mimeType
        return this
    }

    public setDescription(description?: string): this {
        this.description = description
        return this
    }

    public setReplyMarkup(replyMarkup?: InlineKeyboardMarkup): this {
        this.reply_markup = replyMarkup
        return this
    }

    public setInputMessageContent(inputMessageContent?: InputMessageContent): this {
        this.input_message_content = inputMessageContent
        return this
    }

    public setThumbnailUrl(thumbnailUrl?: string): this {
        this.thumbnail_url = thumbnailUrl
        return this
    }

    public setThumbnailWidth(thumbnailWidth?: number): this {
        this.thumbnail_width = thumbnailWidth
        return this
    }

    public setThumbnailHeight(thumbnailHeight?: number): this {
        this.thumbnail_height = thumbnailHeight
        return this
    }

}
